'use strict';
// app 右上角分享
/**
 * todo SHARE
 */
import SET_RIGHTMENU from './SET_RIGHTMENU';
import SHARE from './SHARE';
import SHARE_IMG from './SHARE_IMG';
import SHARE_WEIXIN from './SHARE_WEIXIN';

var name = 'SHARE_RIGHT';
var SHARE_RIGHT = (obj, channel) => {
  if (!obj) {
    if (typeof GOKU.shareObj === 'object') {
      SET_RIGHTMENU('分享', SHARE);
      SHARE_WEIXIN(obj, channel);
    }
  } else if (obj.type === 'img') {
    var {shareItems, shareTitle, imageUrl} = obj;
    SET_RIGHTMENU('分享', _ => SHARE_IMG({shareItems, shareTitle, imageUrl}))
  } else {
    SET_RIGHTMENU('分享', SHARE.bind(this, obj));
    SHARE_WEIXIN(obj, channel);
  }
};
export default SHARE_RIGHT;
